/********************** 
* inventory
***********************/
.device-inventory-wrap {

    div.head {
        position: relative;
        height: 90px;
        background: #97a4b1;

        p.title {
            position: absolute;
            top: 27px; left: 43px;
            font-family: $inter-b;
            font-size: 24px;
            color: #fff;
        }
        p.middle {
            font-size: 16px;
            color: #fff;
            text-align: center;
            line-height: 50px;
            padding-top: 20px;

            span {
                font-family: $inter-b;
                font-size: 42px;
                margin: 0 30px;
                vertical-align: middle;
            }
        }
        div.search {
            position: absolute;
            top: 24px; right: 43px;
            
            div.search-input {
                float: right;
                width: 42px;
                height: 42px;
                border-radius: 23px;
                margin-right: 14px;
                background-color: #e8eef4;
                text-align: right;
                overflow: hidden;
                @include prefix(transition, width .5s ease-in-out, webkit ms);

                &.on {
                    width: 200px;
                }

                input.inventory-search {
                    vertical-align: top;
                    margin-top: 8px;
                    padding-left: 0px;
                    width: 0px;
                    border: 0;
                    background: transparent;
                    font-size: 16px;
                    color: #28313b;
                    @include prefix(transition, width .5s ease-in-out, webkit ms);

                    &::placeholder {
                        color: #97a4b1;
                    }
                    &.on {
                        width: 150px;
                    }
                }
                a.search {
                    display: inline-block;
                    margin: 11px 11px 0 0;
                    width: 20px; height: 20px;
                    background: url('../img/icons/icon_inventory_search.png') 50% 50% no-repeat;
                    @include prefix($bgs, 20px 20px, webkit ms);
                    text-indent: 9999px;
                    overflow: hidden;
                }
            }                
        }
    }
    div.navigation {
        position: relative;
        height: 50px;
        background: #97a4b1;
        border-top: 1px solid rgba(255, 255, 255, 0.3);
        text-align: center;

        nav {
            display: inline-block;
            padding-top: 11px;

            a {
                position: relative;
                display: inline-block;
                margin: 0 28px;
                font-family: $inter-m;
                font-size: 16px;
                color: #fff;
                line-height: 18px;

                &:before {
                    content: '';
                    position: absolute;
                    width: 100%; height: 6px;
                    top: 29px; left: 0;
                    background: #63dbc1;
                    @include prefix(border-radius, 3px, webkit ms);

                }
                &.v::after {
                    content: '';
                    position: absolute;
                    width: 1px; height: 23px;
                    top: -1px; right: -32px;
                    background: rgba(255, 255, 255, 0.3);
                }
            }
        }
    }
    div.inventory-table-wrap {
        width: 1238px;
        margin: 0 auto;
        padding-top: 28px;

        th {

        }
        td {

            a {
                display: inline-block;
                width: 20px; height: 20px;
                text-indent: -9999px;

                &.info {
                    background: url('../img/icons/icon_inventory_info.png') 50% 50% no-repeat;
                    @include prefix($bgs, 20px 20px, webkit ms);
                }
                &.edit {
                    margin: 0 17px;
                    background: url('../img/icons/icon_inventory_edit.png') 50% 50% no-repeat;
                    @include prefix($bgs, 18px 18px, webkit ms);
                }
                &.delete {
                    background: url('../img/icons/icon_inventory_delete.png') 50% 50% no-repeat;
                    @include prefix($bgs, 19px 19px, webkit ms);
                }
            }
        }
    }
}